import React, { Component } from 'react';

class PortFolio extends Component {
  render() {   
    const {title,info,part,link,link2,time} = this.props.data;
    return (
      <div className='eachItem'>
        <div>{title}</div>
        <div>{info}</div>
        <div>{part}</div>
        <div>{time}</div>
        <div>
          <a href={link} target='_blank' rel='noopener noreferrer'>{link2 !== undefined ? 'PC SITE' : 'CLICK'}</a><br />
          {link2 !== undefined && <a href={link2} className="no02" target='_blank' rel='noopener noreferrer'>MOBILE</a>}  
        </div>
      </div>
    );
  }
}

export default PortFolio;
